@import url(https://fonts.googleapis.com/css2?family=Prompt&display=swap);
*{border:0;margin:0;padding:0}html{height:100%}body{font-family:"Prompt",Arial,sans-serif;width:100vw;min-width:77rem;height:100vh;background:#222;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.Header{background:linear-gradient(0deg, #f92470, #26baff);width:100%;height:100%;grid-area:header;box-sizing:border-box;box-shadow:0 5px 10px rgba(0,0,0,0.25);padding:1rem;line-height:4rem}.Logo{height:100%;width:31rem;margin:auto}.LogoImg{float:left;height:100%;margin-right:1rem}.LogoText{color:white;font-size:36px}

.KartOptimizerApp{display:grid;grid-template-areas:'header header' 'config sidebar';grid-template-rows:6rem calc(100vh - 6rem);grid-template-columns:calc(100% - 34rem) 34rem}

.CenterContainer{width:40rem;height:44.25rem}.KartConfig{display:flex;justify-content:center;align-items:center;width:100%;height:100%;grid-area:config;background:#222}.NoBackend{font-size:20px;text-align:center;color:white;line-height:2}.PartList{display:flex;flex-wrap:wrap;justify-content:center}.PartOptions{display:flex}.Separator{background:linear-gradient(-90deg, #f92470, #26baff);width:calc(100% - 1rem);height:0.25rem;border-radius:1rem;margin:1rem 0.5rem}

.PartSelection{box-sizing:border-box;width:9em;height:12.25rem;margin:0.5rem}.PartLock{transition:.2s;margin-top:1rem;padding:0.5rem;border-radius:2rem}.PartLock:hover{-webkit-filter:brightness(70%);filter:brightness(70%)}.PartLabel{color:white;box-sizing:border-box;font-size:18px;text-align:center;white-space:nowrap}.SelectedPart{transition:.2s;display:flex;justify-content:center;align-items:center;box-sizing:border-box;border-radius:2rem;padding:0.5rem;width:9rem;height:9rem}.SelectedPart:hover{-webkit-filter:brightness(70%);filter:brightness(70%)}

.PartTile{display:flex;justify-content:center;align-items:center;transition:.2s;box-sizing:border-box;width:4rem;height:4rem;margin:0.5rem;padding:0.25rem;border-radius:1rem}.PartTile:hover{-webkit-filter:brightness(70%);filter:brightness(70%)}.PartImage{width:100%}

.KartCombo{transition:.2s;width:100%;height:8rem;border-radius:1rem;background:#777;overflow:hidden;margin-bottom:1rem}.KartCombo:hover{-webkit-filter:brightness(70%);filter:brightness(70%)}.Parts{display:flex;justify-content:space-evenly;align-items:center;box-sizing:border-box;padding:1rem;float:right;width:calc(100% - 6rem);height:100%;flex-wrap:wrap}.Parts img{flex:1 1 50%;width:2.9rem}.Stats{box-sizing:border-box;padding:1rem;float:left;width:6rem;height:100%;line-height:1.2rem;background:#222;color:white}

.LoadingKarts{text-align:center;color:white;font-size:20px;margin-top:1rem}.Optimizer{width:100%;background:#444;box-sizing:border-box;padding:0 2rem;height:26.452rem;margin-bottom:2rem}.Options{height:100%;width:calc(100% - 12rem);box-sizing:border-box;overflow-y:scroll;padding:1rem;background:#333;border-radius:2rem}

.Stat{transition:0;color:white;border-radius:1.5rem;padding:0.5rem;font-size:20px;text-align:center;margin:1rem 0;background:#777}.Stat:hover{-webkit-filter:brightness(90%);filter:brightness(90%)}.Stat:first-child{margin-top:0}.Stat:last-child{margin-bottom:0}.StatList{box-sizing:border-box;width:100%;background:#333;padding:1rem;border-radius:2rem}.StatPriority{float:left;width:11rem;height:100%;margin-right:1rem}.StatPriority button{transition:.2s;background:linear-gradient(-90deg, #f92470, #26baff);color:white;border-radius:1.5rem;font-size:20px;text-align:center;font-family:"Prompt",Arial,sans-serif;margin-top:1rem;padding:0.5rem;width:100%}.StatPriority button:hover{-webkit-filter:brightness(70%);filter:brightness(70%)}

.ProgressBar{width:100%;color:white;font-size:20px;margin-bottom:1rem}.ProgressBar p{margin-bottom:0.25rem}.Bar{background:linear-gradient(-90deg, #f92470, #26baff);position:relative;width:100%;border-radius:1rem;height:1rem;overflow:hidden}.SeparatorContainer{display:flex;justify-content:space-evenly;align-items:center;position:absolute;width:100%;height:1rem;top:0}.Progress{position:absolute;right:0;top:0;height:1rem;background:#777}.VerticalSeparator{height:100%;width:2px;background:#333}

.KartStats{box-sizing:border-box;width:100%;height:24rem;padding:2rem}.Sidebar{width:100%;height:100%;grid-area:sidebar;box-sizing:border-box;overflow-y:scroll;background:#444}

